<template>
  <div>
    <center class="bookSearch">
      <el-input
        placeholder="填写作品名或是作者名字搜索"
        v-model.trim="search"
        style="width: 60%"
        element-loading-text="数据用于研究解析电子书格式, 若有侵权, 请发函和权利证明, 本站将积极配合."
        @keyup.enter="update_data()"
      >
        <template #append>
          <el-button icon="el-icon-search" @click="update_data()"></el-button>
        </template>
      </el-input>
    </center>
    <el-row :gutter="20" v-loading.fullscreen.lock="loading">
      <el-col
        v-for="o in books"
        :key="o.content_hash"
        :xs="12"
        :sm="6"
        :md="5"
        :lg="4"
      >
        <div class="book" @click="show_book_info(o)">
          <div class="book-cover">
            <el-image
              style="height: 200px"
              :src="o.cover"
              fit="fill"
              class="image"
            ></el-image>
          </div>
          <div style="padding: 5px" class="book-creator">
            <p style="font-size: var(--el-font-size-extra-small)">
              {{ o.creator }}
            </p>
            <p style="font-size: var(--el-font-size-small)">
              <strong>{{ o.title }}</strong>
            </p>
          </div>
        </div>
      </el-col>
    </el-row>
    <div>
      <el-pagination
        @current-change="update_data"
        :page-size="12"
        layout="prev, pager, next"
        :total="book_count"
      >
      </el-pagination>

      <el-dialog
        v-model="bookinfoVisible"
        :title="currentBook.title"
        size="mini"
        :fullscreen="true"
      >
        <el-descriptions title="书本信息" :column="1" label-align="right">
          <template #extra>
            <el-button type="primary" disabled>全书阅读</el-button>
          </template>
          <template v-for="(value, key) in  currentBook">
            <el-descriptions-item
              :label="key"
              :key="key"
              v-if="key!='extend'"
            >
              <span v-html="value.trim()"></span>
            </el-descriptions-item>
          </template>
        </el-descriptions>
        <br/>
        <el-descriptions title="书本其他信息" :column="1" label-align="right">
          <el-descriptions-item
            :label="key"
            v-for="(value, key) in currentBook.extend || {}"
            :key="key"
          >
            <span v-html="value.trim()"></span>
          </el-descriptions-item>
        </el-descriptions>
        <!-- <div v-for="o in 3" :key="o">
          <br />
          <el-card  v-if="hide_iframe[o]">
            <iframe
              :src="`/${currentBook.store_path}/${o}.html`"
              frameborder="0"
              width="100%"
              height="700px"
              :ref="`iframe_${o}`"
              @load="loading_error(o)"
            ></iframe>
          </el-card>
        </div> -->
      </el-dialog>
      <center>
        <p style="font-size: var(--el-font-size-extra-small);">
          <span> 书籍有 {{book_all_count||0}} 本. </span>
          <span style="color: red">数据来源百度蜘蛛, 仅用于解析电子书格式学习, 本站不存储除封面外书本版权内容.</span>
        </p>
      </center>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Books",
  data() {
    return {
      loading: false,
      search: undefined,
      bookinfoVisible: false,
      currentBook: {},
      books: [],
      book_count: 0,
      book_all_count:0,
      hide_iframe: [false, false, false],
    };
  },
  mounted() {
    this.update_data();
  },
  methods: {
    loading_error(e) {
      let title = this.$refs[`iframe_${e}`].contentDocument.title;
      if(title==='404 Not Found'){
          this.hide_iframe[e]=false
      }
    },
    show_book_info(book) {
      // this.hide_iframe = [true, true, true];
      this.bookinfoVisible = true;
      this.currentBook = book;
    },
    update_data(page) {
      this.loading = true;
      axios
        .get("/book/read", {
          params: {
            length: 12,
            current_page: page || 1,
            search: this.search,
          },
        })
        .then(({ data }) => {
          this.loading = false;
          this.books = data.data.items;
          this.book_count = data.data.recordsFiltered;
          this.book_all_count = data.data.book_all_count;
        });
    },
  },
};
</script>

