.book {
  margin-bottom: 2em;
  width: 200px;
}
.book:hover {
  /* border-radius: 1px solid #eee; */
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.book-cover {
  text-align: center;
}
.book-creator {
  margin-left: 1em;
  margin-right: 1em;
}
.book-creator p {
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.bookSearch {
  margin: 2em;
}
</style>